body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-container {
  padding: 20px;
  min-height: 100%;
  min-height: 100vh;
}

.form-container .title {
  margin-top: 40px;
}

.form-container .title-message {
  margin-top: 20px;
}

.google-btn,
.or-text {
  margin-top: 30px;
}

.or-text {
  color: #bbb;
  font-size: 12px;
}

.login-btn,
.login-btn:hover {
  margin-top: 20px;
  background: #5456ff;
  border-color: #5456ff;
}

.login-btn-separator {
  margin-top: 30px;
  border-bottom: 1px solid #bbb;
}

.signup-link {
  margin-top: 20px;
}

.btn,
.form-control {
  border-radius: 0px !important;
}
